@import url(/node_modules/antd/dist/antd.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.dummyHeader {
  text-align: center;
  margin-right: 80px;
}

.grid {
  display: grid;
  height: 1000px;
  grid-template: 250px 250px 250px 250px / 1fr 1fr 1fr;
  grid-template-areas: "controls timer rightUpper"
                       "controls timer resources"
                       "history history history"
                       "history history history";
}

.App{
  margin-top: 30px;
}
.timer {
  grid-area: timer;
}

.timer {
  vertical-align: center;
  margin-left: 100px;
  padding-top: 30px;
}

.message {
  margin-right: 200px;
  font-size: 30px;
}


.resources {
    grid-area: resources;
}

.list {
    list-style: none;
}
.history {
  margin-left: 20px;
  grid-area: history;
}


.button{
    margin-top: 10px;
    border-color: mistyrose;
    color: black;
    font-size: 10px;
    background: transparent;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
  }

.button:hover {
    background: rgba(202, 202, 202, 0.5);
}

.button:active {
    background: rgba(94, 94, 94, 0.5);
    box-shadow: 0 2px #666;
    transform: translateY(4px);
}

.moreInfo {
    margin-left: 10px;
    font-size: 14px;
}

.infoLabel {
    font-weight: bold;
    display: inline;
}

.card {
    width: 290px;
    margin-right: 20px;
    margin-bottom: 20px;
}
.records {
  grid-area: rightUpper;
}

.records {
  width: 350px;
}

.Controls {
    display: grids;
    grid: 150px / auto auto auto auto;
    margin: 0 auto;
    /* justify-content: left; */
    grid-area: controls;
}

.InputLabels {
    display: flex;
    /* justify-content: left; */
}

.Table {
    display: table;
    margin-left: 0px;
}

b {
    width: 50px;
}

ul#horizontal-list li {
    display:inline-block;
}

.StartButton {
    margin-left: 40px;
    margin-bottom: 10px;
}

.GiveUp {
    margin-left: 40px;
}

.Inputs {
    display: flex;
    
}

#inputForm input{
    width: 50px;
}

.select {
    margin-left: 40px;
    margin-top: 10px;
}

